<template>
    <vue-plyr ref="plyrpreview">
        <div class="plyr__video-embed">
            <iframe
                class="video"
                :src="video_url"
                allowfullscreen
                allowtransparency
                allow="autoplay"
            ></iframe>
        </div>
    </vue-plyr>
</template>

<script>

export default {
    name: 'Player',

    props: ['video_url', 'autoplay'],

    // props: {
    //     video_url: String || Boolean,
    //     autoplay: Boolean
    // },

    data() {
        return {
            player: null,
            video: null,
            auto_play: null
        }
    },

    beforeMount(){
        // this.video = `https://peertube.swarm.solvingmaz.es/static/webseed/9cc6877c-80a5-4352-b545-068399dd5ee9-720.mp4`
    },

    mounted(){
        console.log('PLAYER VIDEO', this.video_url, this.autoplay)

        if(this.autoplay) {
            this.player = this.$refs.plyrpreview.player
            console.log('PLAYER.ON', this.player)

            this.player.on('ready', () => {
                this.player.muted = true
                this.player.play()
                // try {
                //     this.player.play()
                // } catch (error) {
                //     console.log('ERROR PLAY', error)
                // }
            })
        }
    }
}
</script>

