<template>
  <div class="chatIcon">   
    <userspic :users="users" :unseen="this.chat.getUnreadNotificationCount()"/>
  </div>
</template>

<style scoped lang="sass">


</style>

<script>


import _ from "underscore";
import f from "@/application/functions";

export default {
  name: 'chatIcon',

  props : {
    chat : Object
  },

  computed:{
    users : function(){

        var users = _.filter(_.map(this.chat?.getJoinedMembers() || [], (m) => {

            return this.$f.deep(this, '$store.state.users.' + m.name) || null

        }), (m) => {
            return m && m.id != this.core.user.userinfo?.id
        })


        console.log('users', users)

        return users
      
    },
  }
}
</script>
