import {mapState} from 'vuex';
import f from "@/application/functions";
import _ from 'underscore'

export default {
  name: 'eventsMember',
  props: {
    event: Object,
    preview: Boolean,
    userinfo: Object,
  },

  data: function () {

    return {
      loading: false,
      pName: Object
    }

  },

  computed: mapState({
    auth: state => state.auth,
    keyword: function () {
      let pStateUsers = this.$store.state.users
      var membership = this.event.event?.content.membership

      if (membership === 'join') return "joined in the chat"
      if (membership === 'invite') {
        console.log( this.event.event, "eveeeent")
        let invitedUserID = this.event.event.state_key
        let invitedName = ''
         _.mapObject(pStateUsers,function (key, value){
             if('@' + key.id + ':matrix.pocketnet.app' === invitedUserID){
               console.log(key)
               return invitedName = key.name
             }
        })
        console.log(invitedName)
        return "invited " + `${invitedName}` + " in the chat"
      }
      if(membership === 'leave'){
        let kickedUser = this.event.event.content.displayname
        let name = ''
        _.mapObject(pStateUsers,function (key, value){
          if(key.id === kickedUser){
            name = key.name
          }
        })
        return 'kicked' + ' ' + name
      }

      return "Leave"

    }
  }),

  methods: {},
}