<template>
  <div class="event" ref="msgElement">
    <member :chat="chat" :event="event" :userinfo="userinfo" :readed="readed" :preview="preview || false"
            v-if="type === 'member'" />
    <message @openGalleryImg="openImage" :event="event._clearEvent" :imgEvent="galleryData" :userinfo="userinfo"
             :filePreview="filePreview" :baseImg="prevImg" :readed="readed" @openImg="openImage"
             :preview="preview || false"
             :clientWidth="clientWidth"
    v-if="type === 'message'"/>
    <common :event="event._clearEvent" :userinfo="userinfo" :readed="readed" :preview="preview || false"
            v-if="type === 'common'"/>
  </div>
</template>

<script>
import common from '@/components/events/event/common/index.vue'
import member from '@/components/events/event/member/index.vue'
import message from '@/components/events/event/message/index.vue'

export default {
  name: 'event',

  components: {
    common,
    member,
    message,
  },

  data: function () {
    return {
      readed: false,
    }
  },


  props: {
    event: Object,
    preview: Boolean,
    prevImg: {
      type: String,
      default: 'empty'
    },
    chat: Object,
    filePreview: {type: Object,
      default: function () {
        return {
          show: false
        }
      }},
    metaUrl: String,
    galleryData: {},
    // add_image: Function,
    goToGallery: Function,
    clientWidth: Number
  },

  computed: {
    type: function () {

      /*console.log('this.event', this.event, this.event.getSenderKey())
      debugger*/

      var event = this.event.event

      var t = event.type


      if (['m.room.member'].indexOf(t) > -1) return 'member'
      if (['m.room.encrypted'].indexOf(t) > -1) return 'message'
      // if (['m.room.image'].indexOf(t) > -1) return 'image'
      // if (['m.room.file'].indexOf(t) > -1) return 'file'
      return 'common'

    },

    userinfo: function () {

      return this.$f.deep(this, '$store.state.users.' + this.$f.getmatrixid(this.event.event.sender)) || {}

    },



  },
  mounted: function () {
    // console.log('this.event.event', this.event.event)
    this.checkReaded()
  },

  methods: {
    checkReaded: function () {
      if (this.event)
        this.core.mtrx.isReaded(this.event).then(readed => {

          this.readed = !!readed
        })
    },
    openImage: function (img) {
      this.$emit('openImageEvent', img)
    },
  }
}
</script>
<style lang="sass">
.event
  position: relative
</style>
